'use strict';

/**
* Return a list of Z-test alternative hypotheses.
*
* @module @stdlib/stats-base-ztest-alternatives
*
* @example
* import alternatives from '@stdlib/stats-base-ztest-alternatives';
*
* var list = alternatives();
* // e.g., returns [ 'two-sided', 'less', 'greater' ]
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import enumeration from './enum.js';


// MAIN //

setReadOnly( main, 'enum', enumeration );


// EXPORTS //

export default main;

